import Vue from 'vue';
import Router from 'vue-router';
import TimeMachine from '@/components/TimeMachine';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '',
      name: 'TimeMachine',
      component: TimeMachine,
    },
  ],
});



